/**  */
const config = {
  // 
  appNames: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 6, label: ''}],
  status: [{value: true, label: ''}, {value: false, label: ''}],
  skip: [{value: true, label: ''}, {value: false, label: ''}],
  userTags: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 6, label: ''}, {value: 7, label: '7'}],
  terminals: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: 'IOS'}],
  positions: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 6, label: ''}, {value: 7, label: ''}, {value: 8, label: ''}, {value: 9, label: ''}],
  displayPositions: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}],
  versions: [{value: 1, label: '1.8.0'}, {value: 2, label: '1.8.2'}, {value: 3, label: '1.8.3'}],
  configTypes: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}],
  configTypeImages: [{value: 1, label: 'http://fddimage.51huihuahua.com/image/cash-ithdrawal-switch-prompt.png'}, {value: 2, label: 'http://fddimage.51huihuahua.com/image/queue-page-switch.png'}, {value: 3, label: 'http://fddimage.51huihuahua.com/image/show-description-in-loan.png'}, {value: 4, label: 'http://fddimage.51huihuahua.com/image/alipay-repayment-considerations.png'}]
}

const formatAppNume = function (row, col, val) {
  let label = null
  config.appNames.forEach(appName => {
    if (appName.value === val) {
      label = appName.label
    }
  })
  return label
}

const formatUserTags = function (row, col, val) {
  let label = null
  config.userTags.forEach(userTag => {
    if (userTag.value === val) {
      label = userTag.label
    }
  })
  return label
}

const formatStatus = function (row, col, val) {
  return val ? '' : ''
}

const formatSkip = function (row, col, val) {
  if (val) {
    return ''
  }
  return ''
}

const formatTerminals = function (row, col, val) {
  let label = null
  config.terminals.forEach(terminal => {
    if (terminal.value === val) {
      label = terminal.label
    }
  })
  return label
}

const formatVersions = function (row, col, val) {
  let label = null
  config.versions.forEach(version => {
    if (version.value === val) {
      label = version.label
    }
  })
  return label
}

const formatConfigTypes = function (row, col, val) {
  let label = null
  config.configTypes.forEach(configType => {
    if (configType.value === val) {
      label = configType.label
    }
  })
  return label
}

const formatPositions = function (row, col, val) {
  let label = null
  config.positions.forEach(position => {
    if (position.value === val) {
      label = position.label
    }
  })
  return label
}

const formatDisplayPositions = function (row, col, val) {
  let label = null
  config.displayPositions.forEach(displayPosition => {
    if (displayPosition.value === val) {
      label = displayPosition.label
    }
  })
  return label
}

const configTypeImages = function (row, col, val) {
  let label = null
  config.configTypeImages.forEach(configTypeImage => {
    if (configTypeImage.value === val) {
      label = configTypeImage.label
    }
  })
  return label
}

const formatTimeStamp = function (timeStamp, format) {
  if (!timeStamp) return null
  if (timeStamp < 1000000000000) timeStamp *= 1000
  let date = new Date(timeStamp)
  return formatDate(date, format)
}

const formatDate = function (date, format) {
  if (!date) return null
  if (!format) format = 'yyyy-MM-dd hh:mm:ss'
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    'q+': Math.floor((date.getMonth() + 3) / 3),
    'S': date.getMilliseconds()
  }
  if (/(y+)/.test(format)) {
    format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length))
    }
  }
  return format
}

exports.install = function (Vue, options) {
  Vue.prototype.globalConfig = config
  Vue.prototype.formatStatus = formatStatus
  Vue.prototype.formatSkip = formatSkip
  Vue.prototype.formatUserTags = formatUserTags
  Vue.prototype.formatTerminals = formatTerminals
  Vue.prototype.formatVersions = formatVersions
  Vue.prototype.formatTimeStamp = formatTimeStamp
  Vue.prototype.formatDate = formatDate
  Vue.prototype.formatAppNume = formatAppNume
  Vue.prototype.formatConfigTypes = formatConfigTypes
  Vue.prototype.configTypeImages = configTypeImages
  Vue.prototype.formatPositions = formatPositions
  Vue.prototype.formatDisplayPositions = formatDisplayPositions
}



